import classNames from 'classnames';
import { forwardRef, useCallback, useMemo, type InputHTMLAttributes } from 'react';

import {
  DesignSystemEventProviderAnalyticsEventTypes,
  DesignSystemEventProviderComponentTypes,
  getInputStyles,
  useDesignSystemEventComponentCallbacks,
  useDesignSystemSafexFlags,
  useDesignSystemTheme,
  XCircleFillIcon,
} from '../../design-system';
import type { AnalyticsEventProps, ValidationState } from '../../design-system/types';
import { safex } from '../../design-system/utils/safex';
import { useCallbackOnceUntilReset } from '../../design-system/utils/useCallbackOnceUntilReset';

export interface DatePickerInputProps
  extends Omit<InputHTMLAttributes<HTMLInputElement>, 'prefix' | 'suffix'>,
    AnalyticsEventProps<
      DesignSystemEventProviderAnalyticsEventTypes.OnValueChange | DesignSystemEventProviderAnalyticsEventTypes.OnView
    > {
  showTimeZone?: boolean;
  validationState?: ValidationState;
  prefix?: React.ReactNode;
  suffix?: React.ReactNode;
  allowClear?: boolean;
  onClear?: () => void;
}

export const DatePickerInput = forwardRef((props: DatePickerInputProps, ref: React.Ref<HTMLInputElement>) => {
  const emitOnView = safex('databricks.fe.observability.defaultComponentView.input', false);
  const { theme, classNamePrefix } = useDesignSystemTheme();
  const { useNewShadows, useNewFormUISpacing } = useDesignSystemSafexFlags();
  const {
    componentId,
    analyticsEvents,
    showTimeZone,
    validationState,
    prefix,
    suffix,
    allowClear,
    onChange,
    onFocus,
    onKeyDown,
    onClear,
    ...restProps
  } = props;

  const memoizedAnalyticsEvents = useMemo(
    () =>
      analyticsEvents ??
      (emitOnView
        ? [
            DesignSystemEventProviderAnalyticsEventTypes.OnValueChange,
            DesignSystemEventProviderAnalyticsEventTypes.OnView,
          ]
        : [DesignSystemEventProviderAnalyticsEventTypes.OnValueChange]),
    [analyticsEvents, emitOnView],
  );

  const eventContext = useDesignSystemEventComponentCallbacks({
    componentType: DesignSystemEventProviderComponentTypes.Input,
    componentId,
    analyticsEvents: memoizedAnalyticsEvents,
    valueHasNoPii: false,
  });

  const { callbackOnceUntilReset: sendAnalyticsEventOncePerFocus, reset: resetSendAnalyticsEventOnFocus } =
    useCallbackOnceUntilReset(eventContext.onValueChange);

  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      sendAnalyticsEventOncePerFocus();
      if (e.target.value) {
        onChange?.(e);
      }
    },
    [onChange, sendAnalyticsEventOncePerFocus],
  );

  const handleClear = useCallback(() => {
    sendAnalyticsEventOncePerFocus();
    onClear?.();
  }, [onClear, sendAnalyticsEventOncePerFocus]);

  const handleFocus = useCallback(
    (e: React.FocusEvent<HTMLInputElement>) => {
      resetSendAnalyticsEventOnFocus();
      onFocus?.(e);
    },
    [onFocus, resetSendAnalyticsEventOnFocus],
  );

  return (
    <div
      className={classNames(
        `${classNamePrefix}-input-affix-wrapper`,
        restProps.className,
        restProps.disabled ? `${classNamePrefix}-input-affix-wrapper-disabled` : '',
      )}
      css={[
        getInputStyles(
          classNamePrefix,
          theme,
          {
            validationState,
            type: 'date',
            hasValue: Boolean(restProps.value),
            useNewShadows,
            useNewFormUISpacing,
          },
          {
            useFocusWithin: true,
          },
        ),
        {
          '*::-webkit-calendar-picker-indicator': { display: 'none' },
          [`.${classNamePrefix}-input-prefix`]: {
            ...(!restProps?.disabled && { color: `${theme.colors.textPrimary} !important` }),
          },
          [`&.${classNamePrefix}-input-affix-wrapper > *`]: {
            height: theme.typography.lineHeightBase,
          },
          ...(showTimeZone && {
            [`.${classNamePrefix}-input-suffix`]: {
              display: 'inline-flex',
              flexDirection: 'row-reverse',
              gap: theme.spacing.sm,
              alignItems: 'center',
            },
          }),
        },
      ]}
    >
      {prefix && <span className={`${classNamePrefix}-input-prefix`}>{prefix}</span>}
      <input
        placeholder="Select Date"
        {...restProps}
        onChange={handleChange}
        onFocus={handleFocus}
        value={restProps.value ?? ''}
        ref={ref}
        className={`${classNamePrefix}-input`}
        onKeyDown={(e) => {
          if (e.key === 'Backspace') {
            handleClear();
          }
          onKeyDown?.(e);
        }}
      />
      {suffix && <span className={`${classNamePrefix}-input-suffix`}>{suffix}</span>}
      {allowClear && restProps.value && (
        <button
          aria-label="Clear date"
          type="button"
          css={{
            background: 'none',
            border: 'none',
            display: 'inline-flex',
            justifyContent: 'center',
            alignItems: 'center',
            marginLeft: `${theme.spacing.sm}px !important`,
            padding: 0,
          }}
          onClick={(e) => {
            e.stopPropagation();
            e.preventDefault();
            handleClear();
          }}
        >
          <XCircleFillIcon className={`${classNamePrefix}-input-clear-icon`} />
        </button>
      )}
    </div>
  );
});
